import "./styles.css";
import katara from "../src/images/Katara3.png";
import suki from "../src/images/suki.png";
import waterball from "../src/images/water.png";
import React, { useState } from "react";
import Button from "react-bootstrap/Button";
import "bootstrap/dist/css/bootstrap.min.css";

export default function App() {
  const [components, setComponents] = useState([]);
  const [position, setPosition] = useState({});

  function randomIntFromInterval(min, max) {
    // min and max included
    return Math.floor(Math.random() * (max - min + 1) + min);
  }

  function generatePosition() {
    const top = randomIntFromInterval(50, 200);
    return { top: top + "px" };
  }

  const shootWater = () => {
    console.log("I was clicked");
    setComponents([]);
    const newComponents = [...components, waterball];
    console.log(position);
    setPosition(generatePosition());

    setComponents(newComponents);
  };

  return (
    <div className="App">
      <h1 class="char-name">AVATAR THE FIRST AIRBENDAH WE KNEW</h1>
      <button onClick={() => shootWater()}>Suki is a sidepiece</button>
      <div class="container">
        <div class="row">
          <div class="col-3">
            {" "}
            <div className="katara">
              <img className="katara-body" alt=" " src={katara}></img>
              <p class="char-name">Katara</p>
            </div>
          </div>
          <div class="col-6"></div>
          <div class="col-3">
            {" "}
            <div className="suki">
              <img className="suki-body" alt=" " src={suki}></img>
              <p class="char-name">Suki</p>
            </div>
          </div>
        </div>
      </div>
      <div className="field">
        <div>
          {components.length !== 0 &&
            components.map((waterball, i) => (
              <img
                className="waterball"
                style={position}
                src={waterball}
                alt=""
                key={i}
              ></img>
            ))}
        </div>
      </div>
    </div>
  );
}
